
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'StateList',
components: {
Flexbox,
FlexboxItem
},
props: {
stateList: {
type: Array,
require: true
}
},
methods: {
getMg: function(index) {
if(index != this.stateList.length - 1) {
return {
'margin': '0 1.5rem 0 0'
}
}
return {}
}
}
}



